<template>
   <div class="col-lg-8" style="width:500px;">
    <v-row class="mb-10">
        <v-col>
            <strong 
                class="text-h3 d-flex justify-center" 
                style="width:100%;"
                >
                 New User
            </strong>
        </v-col>      
    </v-row>    
    <v-form>
      <v-col cols="12">
          <v-text-field
            v-model="email"
            :rules="[rules.required, rules.email]"
            label="E-mail"
            prepend-inner-icon="mdi-email"
          ></v-text-field>
        </v-col>
 
        <v-col cols="12">
          <v-text-field
            label="Email or Username"
            prepend-inner-icon="mdi-account"
            v-model="username"
          ></v-text-field>
        </v-col>
        <v-col cols="12">
          <v-text-field
            :prepend-inner-icon="!password?'mdi-lock':'mdi-lock-open'"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"           
            :type="show ? 'text' : 'password'"            
            label="Password"  
            @click:append="show = !show"
            v-model="password"
          ></v-text-field>
        </v-col>
        <v-col cols="12" class="d-flex align-center">            
            <v-btn block class="green white--text" @click="createAccount">Create Account</v-btn>            
        </v-col>
    </v-form>  
  </div>
</template>

<script>
import account from "@/api/service/account.js";

export default {
  name:'SignUp',

  data() {
    return {
      show: false,
      username:'',
      password: '',
      email: '',
        rules: {
          required: value => !!value || 'Required.',
          counter: value => value.length <= 20 || 'Max 20 characters',
          email: value => {
            const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            return pattern.test(value) || 'Invalid e-mail.'
          },
        },
    };
  },


  methods:{
    createAccount(){
      account.signUp(this.username,this.password, this.email)
      .then(user =>{
        console.log(user);
        this.$router.push('/dashboard');  // 注册成功后跳转到 仪表盘
      })
      .catch( error=>{
        console.log(error);    // 此处仅在控制台输出注册不成功的错误信息，完善的功能请自行补充
      });        
    },

  },

}
</script>

