import LC from 'leancloud-storage';


/**
 * @description:  用户登录 
 * @param {*} username
 * @param {*} password
 * @return {*}
 */
let login = (username, password)=>{
    return new Promise((resolve, reject)=>{
        LC.User.logIn(username, password)
        .then(user=>{
            console.log(user);
            resolve(user);    // resolve()返回成功之后的结果，被 then()接收
        }, error=>{
            console.log(error);
            reject(error);    // reject() 返回失败的信息， 被catch()接收
        }).catch(error=>{
            reject(error); 
        });

    });
};

/**
 * @description: 判断是否有已经登录的账号
 * @param {*}
 * @return {*}
 */
let isLoggedIn=()=>{
    let currentUser = LC.User.current();
    if(currentUser){
        console.log(currentUser);
        return currentUser;  //已登录返回当前用户
    }
    return false;    // 没有登录，返回false
};

/**
 * @description: 主动登出用户
 * @param {*}
 * @return {*}
 */
let logout = ()=>{
    LC.User.logOut();
    console.log(LC.User.current());
};

/**
 * @description:  注册新用户，成功的在后继 .then() 进行处理，失败的在 .catch()处理
 * @param {*} username
 * @param {*} password
 * @param {*} email
 * @return {*}
 */
let signUp = (username, password, email='')=>{
    let user = new LC.User();    // 实例化一个用户
    user.setUsername(username);  // 设置用户名
    user.setPassword(password);  // 设置用户密码
    if(email){
        user.setEmail(email);
    }

    return new Promise((resolve, reject)=>{
        user.signUp()
        .then(user=>{
            console.log("注册成功");
            resolve(user);  // 返回成功注册后的用户信息
        }, error=>{
            console.log("注册不成功：", error);
            reject(error); // 返回注册失败的错误信息，此处一般为用户名或邮箱已被使用
        })
        .catch(error=>{
            console.log("注册过程中出现意外： ",error);
            reject(error);    // 返回其他意外导致的错误信息，如网络问题等
        });
    });
};



export default {
    login,
    isLoggedIn,
    logout,
    signUp,
}